.aboutus-sec {
  .about-content {
    padding-top: 50px;
  }
  h1 { padding-bottom: 25px;}
  p { 
    margin-bottom: 0;
    padding-bottom: 15px;
    font-size: 18px; 
    line-height: 26px;
    font-weight: $font_weight_500; 
    &:last-child { padding: 0;}
  }
  .about-banner {
    padding-top: 50px;
    img {
      width: 100%;
      height: 350px;
      object-fit: cover;
    }
  }
  .double-sec {
    h2 { padding-bottom: 35px;}
    .grey-logo {
      text-align: right;
      img { width: 90px;}
    }
    .about-righ-img {
      width: 100%;
      height: 650px;
      object-fit: cover;
    }
  }
  .blue-blk {
    padding: 100px 0;
    background: $color_blue;
    text-align: center;
    h2 { 
      font-size: 48px;
      line-height: 58px;
      color: $color_white;
      font-weight: $font_weight_300;
    }
  }
  .about-gallery-slider {
    .slider-img {
      height: 300px;
      background-size: cover !important;
      background-position: center center !important;
      background-repeat: no-repeat !important;
    }
    .slick-list {
      padding: 0px 50px !important;
      .slick-slide {
        padding: 0px 15px;
      }
    }
  }
  /* Responsive */
  @media ( min-width: 1200px) and ( max-width: 1499px){ 
    h2 { font-size: 28px; line-height: 35px;}
    p { 
      font-size: 18px; 
      line-height: 28px;
    }
    .double-sec {
      .about-righ-img { height: 550px;}
    }
    .blue-blk {
      padding: 80px 0;
      h2 { font-size: 34px; line-height: 42px;}
    }
    .about-gallery-slider {
      .slider-img { height: 250px;}
    } 
  }

  @media ( min-width: 992px) and ( max-width: 1199px){ 
    .about-content {
      padding-top: 40px;
    }
    h1 { padding-bottom: 15px;}
    h2 { font-size: 22px; line-height: 28px;}
    p { 
      font-size: 16px; 
      line-height: 25px;
    }
    .about-banner {
      padding-top: 40px;
      img { height: 250px;}
    }
    .double-sec {
      h2 { padding: 0 0 15px;}
      .about-righ-img { height: 450px;}
    }
    .blue-blk {
      padding: 70px 0;
      h2 { font-size: 30px; line-height: 38px;}
    }
    .about-gallery-slider {
      .slider-img { height: 200px;}
    } 
  }

  @media ( min-width: 768px) and ( max-width: 991px){
    .about-content {
      padding-top: 40px;
    }
    h1 { padding-bottom: 15px;}
    h2 { font-size: 18px; line-height: 25px;}
    p { 
      font-size: 14px; 
      line-height: 22px;
    }
    .about-banner {
      padding-top: 40px;
      img { height: 250px;}
    }
    .double-sec {
      h2 { padding: 0 0 15px;}
      .about-righ-img { height: 100%;}
    }
    .blue-blk {
      padding: 50px 0;
      h2 { font-size: 28px; line-height: 35px;}
    }
    .about-gallery-slider {
      .slider-img { height: 165px;}
    } 
  }

  @media ( max-width: 767px) {
    .about-content {
      padding-top: 35px;
    }
    h1 { padding-bottom: 0; text-align: center;}
    h2 { padding: 15px 0; font-size: 18px; line-height: 25px; text-align: center;}
    p { 
      font-size: 14px; 
      line-height: 22px;
      text-align: center;
    }
    .about-banner {
      padding-top: 35px;
      img { height: 150px;}
    }
    .double-sec {
      h2 { padding: 0 0 15px;}
      .grey-logo { margin-bottom: 25px; text-align: center;}
      .about-righ-img { height: 100%;}
    }
    .blue-blk {
      padding: 35px 0;
      h2 { padding: 0; font-size: 22px; line-height: 28px;}
    }
    .about-gallery-slider {
      .slider-img { height: 165px;}
    } 
  }
}