.contact-sec {
  .contact-details-blk {
    padding: 70px 0 35px;
    .contact-info {
      margin-right: 20px;
      padding: 40px 25px 20px;
      border: 1px solid #dadada;
      border-radius: 12px;
      position: relative;
      text-align: center;
      width: calc(100% / 2 - 10px);
      display: inline-block;
      &:last-child { margin: 0;}
      .contact-ic {
        position: absolute;
        top: -25px;
        left: 50%;
        transform: translateC(-50%);
        -webkit-transform: translateX(-50%);
        background: $color_blue;
        height: 44px;
        width: 44px;
        border-radius: 50%;
        display: inline-flex;
        align-items: center;
        -webkit-align-items: center;
        justify-content: center;
        -webkit-justify-content: center;
        box-shadow: 7px 7px 34px rgba(0,0,0,0.20);
      }
      a { 
        font-size: 18px;
        line-height: 15px;
        color: $color_light_black;
        font-weight: $font_weight_500;
      }
    }
  }
  .contact-form {
    padding: 35px;
    background: $color_white;
    box-shadow: 6px 6px 30px rgba(0,0,0,0.08);
    border-radius: 16px;
    position: relative;
    z-index: 10;
    h3 { padding-bottom: 18px;}
    .btn {
      margin-top: 15px;
      width: 100%;
    }
  }
  .footer-top {
    background: none;
    h4 { color: $color_light_black; font-weight: $font_weight_500;}
    .social-ic {
      padding: 0 0 25px;
      li {
        .nav-link {
          height: 40px;
          width: 40px;
          border-color: #dadada;
          color: $color_blue !important;
          font-size: 20px;
          line-height: 25px;
        }
      }
    }
  }
  @media ( min-width: 992px) and ( max-width: 1199px) { 
    .contact-details-blk {
      padding: 50px 0 35px;
      .contact-info {
        padding: 35px 15px 15px;
        a { font-size: 15px; line-height: 22px;}
      }
    }
    .contact-form {
      padding: 25px 15px;
      h3 { font-size: 18px; line-height: 27px;}
    }
  }
  @media ( min-width: 768px) and ( max-width: 991px) {
    .contact-details-blk {
      padding: 50px 0 35px;
    }
  }
  @media ( max-width: 767px) {
    .contact-details-blk {
      padding: 35px 0 0;
      .contact-info {
        margin: 15px 0 50px;
        width: 100%;
        display: block;
      }
    }
    .contact-form {
      padding: 25px 15px;
      h3 { font-size: 16px; line-height: 22px; text-align: center;}
    }
  }
}
/* Map Styling */
.map-blk {
  margin-top: -150px;
  position: relative;
  &::before {
    content: '';
    background: -moz-linear-gradient(top, rgba(255,255,255,1) 0%, rgba(255,255,255,0) 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(top, rgba(255,255,255,1) 0%,rgba(255,255,255,0) 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom, rgba(255,255,255,1) 0%,rgba(255,255,255,0) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    height: 100px;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    z-index: 1;
  }
  .map-info { 
    display: block; 
    img {
      height: 64px; width: 64px;
    }
    .map-info-text {
      padding: 10px;
      height: 100%;
      width: 200px;
      background: $color_white;
      border-radius: 8px;
      box-shadow: 6px 10px 20px rgba(0,0,0,0.08);
      position: relative;
      &::before { bottom: -20px; left: 20px; border: solid transparent; content: " "; height: 0; width: 0; position: absolute; pointer-events: none; border-bottom-color: $color_white; border-width: 10px; transform: rotate(-180deg); -webkit-transform: rotate(-180deg);}
      img { width: 20px; height: auto;}
      p { margin: 5px 0 0; font-family: 'Montserrat', sans-serif !important; font-size: 12px; line-height: 19px; color: $color_light_black;}
      a { padding-right: 10px; text-decoration: underline; color: $color_blue; display: inline-block;}
      .btn {
        background: transparent;
        border: none;
        padding: 0 0 0 10px;
        img { width: 10px;}
        position: absolute;
        top: 0;
        right: 5px;
      }
    }
  }
  .gm-fullscreen-control,
  .gmnoprint { display: none;}
}