.artist-sidebar { display: none;}
.artist-header{
  &.mobile-menu { display: none;}
  position: fixed; top: 0; left: 0; width: 100%; z-index: 10;
  padding: 10px 15px; background: #fff; box-shadow: inset 0px -1px 0 0 rgba(198, 198, 198, 0.50);
  .navbar { padding: 0;}
  .nav-item {
    padding: 0px 15px;
    display: inline-block;
    .nav-link{ 
      &.active {
        color: $color_blue;
        img {object-position: 0 -20px; }
      }
    }
    .nav-link {
      padding: 0; font-weight: 500; font-size: 16px !important; line-height: 19px; color: #464546; text-transform: capitalize;
      img { margin-right: 12px; object-fit: none; object-position: 0 0px; width: 20px; height: 20px;}
      &:hover img { object-position: 0 -20px;}
      &.paymet-ic {
        img { height: 16px;}
        &:hover img { object-position: 0 -16px;}
      }
    }
  }
  .artist-header-right {
    .nav-link {
      img { margin-right: 0;}
    }
    .artist-account {
      .artist-img {
        margin-right: 10px;
        height: 48px;
        width: 48px;
        border-radius: 50%;
        background-size: cover !important;
        background-position: center center !important;
        background-repeat: no-repeat !important;
      }
      h6 {
         font-weight: $font_weight_600;
         i { padding-left: 5px;}
         color: $color_light_black;
        }
      p { margin: 0; color: $color_grey; font-weight: $font_weight_600;}
      &:hover {
        h6 { color: $color_blue;}
      }
    }
  }
  @media (min-width: 1200px) and (max-width: 1499px) { 
    .nav-item {
      padding: 0px 10px;
      .nav-link {
        font-size: 14px;
      }
    }
  }

  @media ( min-width: 992px) and ( max-width: 1199px) { 
    .nav-item {
      padding: 0px 6px;
      .nav-link {
        font-size: 13px !important;
      }
    }
    .artist-header-right {
      .artist-account h6 { 
        font-size: 14px;
        line-height: 19px;
      }
    }
  }

  @media ( min-width: 768px) and ( max-width: 991px) {
    &.mobile-hide { display: none;}
    &.mobile-menu { display: block;}
    padding: 0;
    box-shadow: none;
    .navbar {
      padding: 10px;
      background: $color_blue;
      .navbar-brand img { width: 60px;}
      .artist-header-right {
        width: 100%;
        .nav { 
          justify-content: flex-end; -webkit-justify-content: flex-end;
          .nav-item { 
            padding: 0px 10px;
            .nav-link {
              img {
                object-fit: contain;
                height: 100%;
                object-position: 0 0 !important;
              }
            }
          }
        }
        .artist-account {
          .artist-img {
            margin: 0;
            height: 55px;
            width: 55px;
          }
        }
        .artist-menu {
          .btn {
            padding: 0;
            background: transparent;
            border: none;
          }
        }
      }
    }
  }
  @media ( max-width:767px){
    &.mobile-hide { display: none;}
    &.mobile-menu { display: block;}
    padding: 0;
    box-shadow: none;
    .navbar {
      padding: 10px;
      background: $color_blue;
      .navbar-brand .nav-link img { width: 45px;}
      .artist-header-right {
        .nav-item { 
          padding: 0px 10px;
          .nav-link {
            img {
              object-fit: contain;
              height: 100%;
              object-position: 0 0 !important;
            }
          }
        }
        .artist-account {
          .artist-img {
            margin: 0;
          }
        }
        .artist-menu {
          .btn {
            padding: 0;
            background: transparent;
            border: none;
          }
        }
      }
    }
  }
}
/* Artist Sidebar */
@media ( min-width: 768px) and ( max-width: 991px) { 
  .artist-sidebar {
    padding: 15px 0;
    position: fixed;
    top: 0;
    left: -100%;
    width: 300px;
    max-width: 0;
    height: 100%;
    background: #fff;
    box-shadow: 0px 2px 12px rgba(0,0,0,0.04);
    display: block;
    z-index: 1060;
    transition-duration: .3s;
    border-radius: 0;
    overflow-y: auto;
    &.show {
      max-width: 100%;
      left: 0;
    }
    .nav {
      display: block;
      .nav-item { 
        padding: 12px 15px;
        display: block;
        border-bottom: 1px solid #dadada;
        .nav-link{ 
          padding: 0; font-weight: 500; font-size: 16px !important; line-height: 19px; color: #464546; text-transform: capitalize; display: flex; display: -webkit-flex; align-items: center; -webkit-align-items: center;
          img { margin-right: 12px; object-fit: none; object-position: 0 0px; width: 20px; height: 20px;}
          &:hover img { object-position: 0 -20px;}
          &.paymet-ic {
            img { height: 16px;}
            &:hover img { object-position: 0 -16px;}
          }
          &.active {
            color: $color_blue;
            img {object-position: 0 -20px; }
          }
        }
      }
    }
    .close-toggle {
      padding: 0 15px;
      text-align: right;
    }
  }
}
