.terms-sec {
  h1 { padding-bottom: 25px;}
  p { 
    line-height: 27px;
    &:last-child { margin: 0;}
  }
  a { 
    color: $color_blue;
    text-decoration: underline;
  }
  h3 { padding: 15px 0; text-transform: capitalize;}
  h6 { padding: 10px 0 10px 20px; font-weight: $font_weight_700;}
  ul {
    li {
      padding: 5px 0 5px 15px;
      font-size: 16px;
      line-height: 27px;
      position: relative;
      font-weight: $font_weight_500;
      &::before { content: ''; height: 8px; width: 8px; background: $color_blue; border-radius: 50%; position: absolute; top: 15px; left: 0; display: inline-block;}
      ul {
        li {
          padding: 5px 0 5px 20px;
          position: relative;
          &::before { content: ''; height: 1px; width: 10px; background: $color_light_black; position: absolute; top: 18px; left: 0; display: inline-block;}
        }
      }
    }
  }
  ol {
    margin-left: 20px;
    li { 
      padding: 5px 0;
      font-size: 16px;
      line-height: 27px;
      span {
        padding-right: 10px;
      }
      ol { padding-left: 25px; list-style-type: lower-alpha;}
    }
  }
  /* Responsive */
  @media ( min-width: 768px) and ( max-width: 991px) {
    h1 { padding-bottom: 15px;}
    p { font-size: 14px; line-height: 22px;}
    h3 { padding: 10px 0;}
    ul {
      li {
        font-size: 14px;
        line-height: 22px;
        &::before { height: 7px; width: 7px; top: 13px;}
      }
    }
    ol {
      li { 
        font-size: 14px;
        line-height: 22px;
      }
    }
  }
  @media (max-width: 767px) {
    h1 { font-size: 25px; line-height: 32px; padding-bottom: 15px; text-align: center;}
    p { text-align: center;}
    h3 { padding: 10px 0; text-align: center;}
    h6 { padding-left: 0;}
    ul {
      li {
        font-size: 14px;
        line-height: 22px;
        &::before { height: 5px; width: 5px; top: 13px;}
      }
    }
    ol {
      margin-left: 0;
      li { 
        font-size: 14px;
        line-height: 22px;
      }
    }
  }
  &.privacy-sec {
    h3 { text-align: left;}
  }
}