.order-table {
  .btn-outline { margin: 0; text-transform: capitalize;}
  .table-style {
    th:first-child { min-width: auto;}
  }
  @media ( min-width: 992px) and ( max-width: 1199px) { 
    .table-style { 
      th             { min-width: 180px;}
      th:first-child { min-width: auto;}
    }
  }
  @media ( min-width: 768px) and ( max-width: 991px) { 
    .table-style { 
      th             { min-width: 150px;}
      th:first-child { min-width: auto;}
    }
  }
  @media ( max-width: 767px) { 
    .table-style { 
      margin-bottom: 15px;
    }
  }
}

