.faq-sec{
  h1{ padding-bottom: 25px;}
  .faq-img { text-align: center; }
  .accordion {
    border: none;
    .accordion__item { border-bottom: 1px solid #dadada; border-top: none;} 
    .accordion__button {
      padding: 15px 0;
      background: $color_white;
      border: none;
      position: relative;
      &:focus { outline: none;}
      &::before { margin-right: 0; content: ''; background: url('../../images/open-faq-ic.svg') no-repeat; height: 15px; width: 15px; position: absolute; right: 0px; top: 18px; border: none; transform: rotate(0); -webkit-transform: rotate(0); }
      &[aria-expanded='true']::before, &[aria-selected='true']::before { background: url('../../images/close-faq-ic.svg') no-repeat; height: 2px; width: 15px;}
      .faq-que {
        margin-right: 25px;
        h6 {
          padding: 0;
          font-size: 16px;
          line-height: 19px;
          font-weight: $font_weight_700;
        }
      }
    }
    .accordion__panel {
      padding: 0;
      .faq-ans {
        p { line-height: 25px; text-align: left;}
      }
    }
  }
  /* Responsive Css */
  @media ( min-width: 768px) and ( max-width: 991px) { 
    h1 { padding-bottom: 15px;}
    .accordion {
      margin-bottom: 25px;
      .accordion__panel {
        .faq-ans {
          p { font-size: 14px; line-height: 22px;}
        }
      }
    }
  }
  @media ( max-width: 767px) {
    h1 { padding-bottom: 15px;}
    .faq-img img { width: auto;}
    .accordion {
      margin-bottom: 25px;
      .accordion__button { 
        .faq-que {
          h6 {
            font-size: 14px;
            line-height: 18px;
            text-align: left !important;
          }
        }
      }
      .accordion__panel {
        .faq-ans {
          p { font-size: 12px; line-height: 19px;}
        }
      }
    }
  }
}