.payments-table {
  .table-style {
    th:first-child { min-width: auto;}
  }
  @media ( min-width: 992px) and ( max-width: 1199px) { 
    .double-blk { 
      width: auto !important;
    }
    .table-style { 
      th             { min-width: 180px;}
      th:first-child { min-width: auto;}
    }
  }
  @media ( min-width: 768px) and ( max-width: 991px) { 
    .table-style { 
      th             { min-width: 150px;}
      th:first-child { min-width: auto;}
    }
  }
  @media ( max-width: 767px) { 
    .table-style { 
      margin-bottom: 15px;
    }
  }
}
.table-filter {
  .form-group { 
    margin-right: 30px;
    &.search-product {
      position: relative;
      img { width: 20px; position: absolute; top: 11px; left: 15px;}
      .form-control { width: 280px; padding-left: 40px;}
    }
    .form-control { width: 160px; padding: 8px 15px;}
    label { margin-bottom: 0; margin-right: 12px; text-transform: capitalize; color: $color_grey;}
  }
  .normal-dropdown {
    width: 150px;
    .css-yk16xz-control, .css-1pahdxg-control {
      min-height: 40px !important;
    }
    .css-yk16xz-control .css-g1d714-ValueContainer, 
    .css-1pahdxg-control .css-g1d714-ValueContainer { padding: 3px 15px;}
  }
  .view-select .normal-dropdown { width: 90px;}
  .date-range-picker {
    .form-control {
      width: 100%;
      .date-range-input {
        input { padding: 8px 40px 8px 15px;}
        &::after { top: 9px;}
      }
    }
  }
  .btn-1 { margin-left: 25px; padding: 10px 25px;}

  /* Responsive */
  @media ( min-width: 992px) and ( max-width: 1199px) { 
    flex-wrap: wrap;
    .double-blk { 
      width: calc(100% / 4 - 18px);
    }
    .form-group { 
      margin-right: 18px;
      &.search-product {
        .form-control { width: 100%;}
      }
    }
    .date-range-picker {
      width: 280px;
    }
  }
  @media ( min-width: 768px) and ( max-width: 991px) { 
    flex-wrap: wrap;
    .double-blk { 
      width: calc(100% / 2 - 13px);
      &:nth-child(2n+1) { margin-right: 25px;}
    }
    .form-group { 
      margin-right: 0;
      &.search-product {
        .form-control { width: 100%;}
      }
    }
    .normal-dropdown, 
    .view-select .normal-dropdown,
    .date-range-picker {
      width: 100%;
    }
    .btn-1 { margin: 0; width: 100%;}
  }
  @media ( max-width: 767px) {
    display: block;
    .form-group { 
      margin-right: 0px;
      &.search-product {
        .form-control { width: 100%;}
      }
      .form-control { width: 100%;}
      label {  min-width: 45px;}
    }
    .normal-dropdown, 
    .view-select .normal-dropdown {
      width: 100%;
    }
    .btn-1 { margin: 15px 0; width: 100%;}
  }
}