.notifications-sec {
  background: #f9f9f9;
  .notification-centent {
    width: 864px;
    margin: 0px auto;
    .notification-blks {
      margin: 15px 0;
      .card { 
        margin: 18px 0; padding: 15px; box-shadow: none;
        .notification-ic {
          margin-right: 15px;
          padding: 10px 13px;
          height: 48px;
          width: 48px;
          border-radius: 50%;
          background: #f4f4f4;
        }
        h6 span { font-weight: $font_weight_300;}
        h5 { padding-top: 5px; color: $color_grey;}
      }
    }
  }
  @media ( min-width: 768px) and ( max-width: 991px) { 
    .notification-centent {
      width: 100%;
    }
  }
  @media ( max-width: 767px) {
    .notification-centent {
      width: 100%;
      .notification-blks {
        .card {
          .notification-ic { 
            margin: 0 0 15px 0;
          }
          h5,h6 { text-align: center;}
        }
      }
    }
  }
}