.blog-main-sec {
  .blog-blk {
    margin-top: 30px;
    background: $color_white;
    transition-duration: 0.3s;
    -webkit-transition-duration: 0.3s;
    transform: translate3d(0px, 0px, 0px);
    -webkit-transform: translate3d(0px, 0px, 0px);
    overflow: hidden;
    border-radius: 12px;
    &:hover { 
      box-shadow: 6px 6px 30px rgba(0,0,0,0.08);
      .blog-main-img .blog-img {
        transform: scale(1.1); -webkit-transform: scale(1.1);
      }
    }
    .blog-main-img {
      overflow: hidden;
      border-radius: 12px 12px 0 0;
      .blog-img {
        height: 290px;
        width: 100%;
        background-repeat: no-repeat !important;
        background-position: center center !important;
        background-size: cover !important;
        display: block;
        border-radius: 12px 12px 0 0;
        transition: all 1.2s; 
        -webkit-transition: all 1.2s;
        a{
          display: block;
          height: 100%;
        }
      }
    }
    .blog-excerpt-info {
      padding: 15px;
      border: 1px solid #DADADA;
      border-radius: 0 0 12px 12px;
      border-top: transparent;
      -webkit-border-top: transparent;
      h3 {   
        padding-bottom: 8px;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;  
        overflow: hidden;
        a{
          color: $color_light_black;
          &:hover { color: $color_blue;}
        }
      }
      .auth-nm,
      .publish-dt {
        font-size: 12px;
        line-height: 16px;
        color: #b9b9b9;
        font-weight: $font_weight_500;
      }
      .btn-outline {
        margin-top: 20px;
        padding: 10px 15px;
        border-color: #DADADA;
        letter-spacing: 0;
        color: $color_light_black !important;
        &:hover {
          background: #f9f9f9;
          border-color: #f9f9f9 !important;
        }
      }
    }
  }
  @media ( min-width: 768px) and ( max-width: 991px) {
    .blog-blk { 
      margin-top: 18px;
      .blog-main-img {
        .blog-img {
          height: 200px;
        }
      }
      h3 {
        font-size: 16px;
        line-height: 22px;
      }
    }
  }
  @media ( max-width: 767px) {
    h1 { text-align: center;}
    .blog-blk { 
      margin-top: 18px;
      .blog-main-img {
        .blog-img {
          height: 200px;
        }
      }
      h3 {
        font-size: 16px;
        line-height: 22px;
      }
    }
  }
}

/* Blog Details Page */
.blog-inner-sec {
  .blog-inner-img {
    img {
      height: 450px;
      width: 100%;
      object-fit: cover;
    }
  }
  .blog-inner-content {
    margin: 0px auto;
    padding: 35px 0;
    width: 860px;
    .auth-nm,
    .publish-dt {
      padding: 15px 0;
      font-size: 16px;
      line-height: 22px;
      color: #b9b9b9;
      font-weight: $font_weight_500;
    }
    p { 
      margin-bottom: 20px; line-height: 27px;
      &:last-child { margin: 0;}
    }
    a { color: $color_blue; text-decoration: underline;}
    h3 { padding-bottom: 15px;}
    ul {
      li {
        padding: 5px 0 5px 15px;
        font-size: 16px;
        line-height: 27px;
        position: relative;
        font-weight: $font_weight_500;
        &::before { content: ''; height: 8px; width: 8px; background: $color_blue; border-radius: 50%; position: absolute; top: 15px; left: 0; display: inline-block;}
      }
    }
  }
  @media ( min-width: 992px) and ( max-width: 1199px) { 
    .blog-inner-img {
      img {
        height: 400px;
      }
    }
    .blog-inner-content {
      width: 100%;
    }
  }
  @media ( min-width: 768px) and ( max-width: 991px) { 
    .blog-inner-img {
      img {
        height: 350px;
      }
    }
    .blog-inner-content {
      padding: 25px 0;
      width: 100%;
      h2 { font-size: 18px; line-height: 25px;}
      .auth-nm,
      .publish-dt {
        padding: 12px 0 8px;
        font-size: 14px;
        line-height: 20px;
      }
      p {
        margin-bottom: 15px;
        font-size: 14px;
        line-height: 22px;
      }
      ul {
        li {
          font-size: 14px;
          line-height: 22px;
        }
      }
    }
  }
  @media ( max-width: 767px) { 
    .blog-inner-img {
      img {
        height: 100%;
      }
    }
    .blog-inner-content {
      padding: 25px 0;
      width: 100%;
      h2 { font-size: 16px; line-height: 22px;}
      .auth-nm,
      .publish-dt {
        padding: 12px 0 8px;
        font-size: 13px;
        line-height: 20px;
      }
      p {
        margin-bottom: 15px;
      }
      ul {
        li {
          font-size: 14px;
          line-height: 22px;
          &::before { height: 5px; width: 5px; top: 13px;}
        }
      }
    }
    .blog-pro-imgs {
      img { margin: 10px 0;}
    }
  }
}