.atrist-product-blk {
    .btn-1 {
        padding: 10px 50px;
        background: $color_light_black;
        &:hover { background: $color_blue;}
    }
    .product-price-color {
        display: flex;
        display: -webkit-flex;
        justify-content: flex-end;
        -webkit-justify-content: flex-end;
    }
}
.new-projects-list {
    .filter-applied {
        padding-top: 0 !important;
    }
}
/*Customize Design*/
.iframe-sec {
    iframe { height: 650px;}
    @media ( min-width: 768px) and ( max-width: 1199px) {
        iframe { border-bottom: 1px solid #eeee;}
    }
    @media ( max-width: 767px) {
        iframe { height: 575px; border-bottom: 1px solid #eeee;}
    }
    @media ( max-width: 359px) {
        iframe { height: 477px; border-bottom: 1px solid #eeee;}
    }
}
